import {RouterModule, Routes} from '@angular/router';
import {NgModule} from '@angular/core';
import {IndividualsAndFamiliesComponent} from './individuals-and-families.component';

const routes: Routes = [
    {
        path: '',
        component: IndividualsAndFamiliesComponent,
    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class IndividualsAndFamiliesRoutingModule {}
