import {Component, OnInit, ViewChild} from '@angular/core';

import {LmrRouterLink} from '../models/LmrRouterLink';
import {IndividualFamiliesData} from './individuals-and-families.data';
import {MobileComponent} from '../shared/MobileComponent';
import {Carousel} from "carousel-angular/lib/carousel";

@Component({
    selector: 'app-individuals-and-families',
    templateUrl: './individuals-and-families.component.html',
    styleUrls: ['./individuals-and-families.component.scss']
})
export class IndividualsAndFamiliesComponent extends MobileComponent implements OnInit {

    mobileAboutImg = './assets/svg/mobileAboutImg.svg';
    mobileContactImg = './assets/svg/mobileContactImg.svg';
    mobileHomeImg = './assets/svg/mobileHomeImg.svg';
    mobileHealthServicesImg = './assets/svg/mobileHealthServicesImg.svg';

    healthServicesChildLinks: LmrRouterLink[] = [
        {displayName: 'Home Health Aides', link: '/health-services', linkParams: {service: 'home-health-aides'}},
        {displayName: 'Laboratory Services', link: '/health-services', linkParams: {service: 'laboratory-services'}},
        {displayName: 'Medical Social Work', link: '/health-services', linkParams: {service: 'medical-social-work'}},
        {displayName: 'Occupational Therapy', link: '/health-services', linkParams: {service: 'occupational-therapy'}},
        {displayName: 'Physical Therapy', link: '/health-services', linkParams: {service: 'physical-therapy'}},
        {displayName: 'Skilled Nursing', link: '/health-services', linkParams: {service: 'skilled-nursing'}},
        {displayName: 'Speech Therapy', link: '/health-services', linkParams: {service: 'speech-therapy'}},
        {displayName: 'Private Duty Home Care', link: '/health-services', linkParams: {service: 'private-duty-home-care'}},
        {displayName: 'Private Duty Nursing Care', link: '/health-services', linkParams: {service: 'private-duty-nursing-care'}},
    ];

    navigationLinks: LmrRouterLink[] = [
        {displayName: 'Home', link: '/', imgSrc: this.mobileHomeImg},
        {
            displayName: 'Health Services',
            link: '/health-services',
            imgSrc: this.mobileHealthServicesImg,
            childLinks: this.healthServicesChildLinks
        },
        {displayName: 'Contact Us', link: 'contact-us', imgSrc: this.mobileContactImg},
        {displayName: 'About', link: '/about', imgSrc: this.mobileAboutImg},
    ];

    footerNavigationLinks: LmrRouterLink[] = [
        {displayName: 'Home', link: '/'},
        {displayName: 'About', link: '/about'},
        {displayName: 'Contact Us', link: 'contact-us'},
    ];

    @ViewChild('carousel') carousel: Carousel;

    individualAndFamiliesInfoData = IndividualFamiliesData;

    constructor() {
        super();
    }

    ngOnInit(): void {
        this.onResize();
    }

    slideLeft() {
        this.carousel.prev();
    }

    slideRight() {
        this.carousel.next();
    }
}
